@keyframes slide-in-top {
  0% {
    transform: translateY(-200px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
.cose-icon-child,
.cose-icon-item {
  position: absolute;
  border-radius: var(--br-9xs);
  background-color: var(--color-gray-300);
  width: 0.27rem;
  height: 2.03rem;
  transform-origin: 0 0;
}
.cose-icon-child {
  top: 0.19rem;
  left: 0;
  transform: rotate(-45deg);
}
.cose-icon-item {
  top: 0;
  left: 1.44rem;
  transform: rotate(45deg);
}
.cose-icon {
  position: relative;
  width: 1.63rem;
  height: 1.63rem;
}
.cose-icon-wrapper {
  align-self: stretch;
  height: 2.13rem;
  display: flex;
  flex-direction: row;
  padding: var(--padding-12xs) 0 0;
  box-sizing: border-box;
  align-items: center;
  justify-content: flex-start;
  cursor: pointer;
}
.home2 {
  position: relative;
  line-height: 155.52%;
  font-weight: 500;
  cursor: pointer;
}
.frame-item {
  position: relative;
  width: 0.94rem;
  height: 0.92rem;
}
.hamburger-drawer-child,
.home-parent {
  align-self: stretch;
  box-sizing: border-box;
}
.home-parent {
  height: 2.13rem;
  display: flex;
  flex-direction: row;
  padding: var(--padding-12xs) 0 0;
  align-items: center;
  justify-content: space-between;
}
.hamburger-drawer-child {
  position: relative;
  border-top: 2px solid var(--color-gray-300);
  height: 0.13rem;
}
.all-projects-parent {
  align-self: stretch;
  height: 2.13rem;
  display: flex;
  flex-direction: row;
  padding: var(--padding-12xs) 0 0;
  box-sizing: border-box;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}
.lets-chat {
  position: relative;
  line-height: 155.52%;
  font-weight: 500;
}
.hamburger-drawer {
  position: relative;
  background-color: var(--color-black);
  border: 2px solid var(--color-darkslategray-200);
  box-sizing: border-box;
  width: 100%;
  height: 22.5rem;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  padding: var(--padding-xl) var(--padding-6xl);
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-xl);
  opacity: 0;
  max-height: 90%;
  text-align: left;
  font-size: var(--font-size-lg);
  color: var(--color-white);
  font-family: var(--font-inter);
}
.hamburger-drawer.animate {
  animation: 0.25s ease 0s 1 normal forwards slide-in-top;
}
